<template>
  <div id="order-wait">
    <order-nav></order-nav>
    <h3>待处理工单</h3>
    <el-table
      :data="waitOrder"
      border
      style="width: 80%;margin:0 auto;margin-top:24px"
      max-height="500"
      size="small"
    >
      <el-table-column type="index" label="序号" width="60"></el-table-column>
      <el-table-column prop="initiate_department" label="发单部门"></el-table-column>
      <el-table-column prop="initiate_person" label="发单人"></el-table-column>
      <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
      <el-table-column prop="out_time" label="结单时间"></el-table-column>
      <el-table-column prop="job_type" label="工单类型"></el-table-column>
      <el-table-column prop="need" label="内容需求"></el-table-column>
      <el-table-column label="操作" width="220">
        <template slot-scope="{row}">
          <div>
            <el-button type="primary" size="mini" @click="acceptOrder(row.id)">接取</el-button>
            <el-button type="primary" size="mini" @click="transferOrder(row.id)">转派</el-button>
            <el-button type="danger" size="mini" @click="rejectOrder(row.id)">驳回</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!-- 委派 -->
    <el-dialog :visible.sync="transferVisible" width="30%" center @close="transferClose">
      <!-- 表单部分 -->
      <el-form :model="transferForm" :rules="fromRules" ref="transferRef" label-width="100px">
        <el-form-item label="转派部门" prop="transferDpm">
          <el-select
            v-model="transferForm.transferDpm"
            placeholder="请选择"
            @change="departmentChange"
          >
            <el-option
              v-for="(item,index) in departmentList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="转派人员" prop="transferStaff">
          <el-select v-model="transferForm.transferStaff" placeholder="请选择">
            <el-option v-for="(item,index) in staff" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="transferConfirm">确 认</el-button>
      </span>
    </el-dialog>

    <!-- 驳回 -->
    <el-dialog :visible.sync="rejectVisible" width="30%" center @close="rejectClose">
      <!-- 表单部分 -->
      <el-form :model="rejectForm" :rules="fromRules" ref="rejectRef" label-width="100px">
        <el-form-item label="驳回原因" prop="rejectReason">
          <el-input v-model="rejectForm.rejectReason" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="rejectConfirm">确 认</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      // 获取未处理列表
      waitOrder: [],
      fromRules: {
        transferDpm: [
          {
            type: "string",
            required: true,
            message: "请选择转派部门",
            trigger: "change"
          }
        ],
        transferStaff: [
          {
            type: "string",
            required: true,
            message: "请选择转派人员",
            trigger: "change"
          }
        ],
        rejectReason: [
          { required: true, message: "请填写驳回原因", trigger: "blur" }
        ]
      },
      departmentList: [],
      staff: [],
      // 委派相关
      transferVisible: false,
      transferForm: {
        transferDpm: "",
        transferStaff: "",
        transferId: ""
      },
      // 驳回相关
      rejectVisible: false,
      rejectForm: {
        rejectId: "",
        rejectReason: ""
      }
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取列表
    getWaitOrder() {
      this.$api.getWaitOrder().then(res => {
        console.log(res);
        this.waitOrder = res.data.data;
      });
    },
    // 接取
    acceptOrder(id) {
      console.log(id);
      this.$confirm("是否要接取该工单", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.acceptOrder(id).then(res => {
            console.log(res);
            this.getWaitOrder();
            this.$message.success("接取成功");
          });
        })
        .catch(() => {});
    },
    // 获取部门数据
    getSelectData() {
      this.$api.getOrderSelectData().then(res => {
        console.log(res);
        this.departmentList = res.data.data;
      });
    },
    // 部门选择触发 提供相应员工
    departmentChange(val) {
      this.$api.getReceivePerson(val).then(res => {
        console.log(res);
        this.staff = res.data.data;
      });
    },
    // 转派相关
    transferOrder(id) {
      this.transferForm.transferId = id;
      this.getSelectData();
      this.transferVisible = true;
    },
    transferConfirm() {
      this.$refs.transferRef.validate(state => {
        if (!state) return;
        this.$api.transferOrder(this.transferForm).then(res => {
          console.log(res);
          this.getWaitOrder();
          this.$message.success("转派成功");
          this.transferVisible = false;
        });
      });
    },
    transferClose() {
      this.$refs.transferRef.resetFields();
    },

    // 驳回相关  目前先不需要原因
    rejectOrder(id) {
      // this.rejectForm.rejectId = id;
      // this.rejectVisible = true;
      this.$confirm("是否要驳回该工单", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.rejectOrder(id).then(res => {
            console.log(res);
            this.getWaitOrder();
            this.$message.success("驳回成功");
          });
        })
        .catch(() => {});
    },
    rejectConfirm() {
      this.$refs.rejectRef.validate(state => {
        if (!state) return;
        this.$api.rejectOrder(this.rejectForm).then(res => {
          console.log(res);
          this.getWaitOrder();
          this.$message.success("驳回成功");
          this.rejectVisible = false;
        });
      });
    },
    rejectClose() {
      this.$refs.rejectRef.resetFields();
    }
  },
  created() {
    this.getWaitOrder();
  },
  mounted() {}
};
</script>
<style scoped>
#order-wait {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
/* @import url("../../../../static/css/thead_fixed_table.css"); */
table {
  padding-top: 25px;
}
thead {
  display: table-header-group;
}
tbody {
  display: table-row-group;
}
tbody td:last-child {
  width: 230px;
}
h3 {
  text-align: center;
}
</style>


